@font-face {
  font-family: "TSans";
  src: url("../fonts/TSans.woff2") format("woff2-variations");
  font-weight: 100 900; /* Диапазон весов */
  font-style: oblique 0deg 10deg; /* Для вариативного наклона */
  font-display: swap;

  /* Для расширенной совместимости */
  font-variation-settings: "wght" 600, /* Базовая толщина */ "slnt" 0; /* Базовая наклонность */
}
